import styled from "styled-components";
import { Icon } from "./Icon";

export const Logo = styled(Icon).attrs({
  name: "logo"
})`
  fill: none;
  stroke: #E2A480;
  stroke-width: 1;
  > :nth-child(4){
    fill: #E2A480;
    stroke: none;
  }
  > :nth-child(5){
    fill: #E2A480;
    stroke: none;
`;



